<script setup>
import { ref, onMounted } from "vue";

// Определяем реактивные переменные для данных пользователя
const username = ref("");
const firstName = ref("");
const lastName = ref("");

// Инициализируем данные о пользователе после монтирования компонента
onMounted(() => {
  if (window.Telegram && window.Telegram.WebApp) {
    window.Telegram.WebApp.ready();

    // Извлекаем данные о пользователе
    const user = window.Telegram.WebApp.initDataUnsafe?.user;

    if (user) {
      username.value = user.username || "No username";
      firstName.value = user.first_name || "No first name";
      lastName.value = user.last_name || "No last name";
    }
  } else {
    console.error("Telegram WebApp API не доступен.");
  }
});
</script>

<template>
  <header>
    <div class="container">
      <div class="header-content">
        <div class="username-wrapper">@username</div>
        <div class="settings-wrapper">Settings</div>
      </div>
    </div>
  </header>
</template>

